import React, {useState} from "react";
//Esta biblioteca se puede usar para el hash de la contrasea, se instala con npm install crypto-browserify
//Pero me est dando problemas porque webpack 5 no lo soporta, as que lo dejo comentado
// import {createHash} from "crypto-browserify";

//Usar la Web Crypto API mejor, es ms proceso, pero funciona

//Para los links
import {Link, useNavigate} from "react-router-dom";

import { BarraSuperiorInicio } from "./BarraSuperiorInicio";
import { FooterPG } from "./Footer";
import "../CSS/contenedor.css";

const FormsLogin = () => {
    const [username, setUsername] = useState("");
    const [password, setPassword] = useState("");

    //
    const navigate = useNavigate();

    //Para convertir el ArrayBuffer a hexadecimal
    const aBufferToHex = (buffer) => {
      //Uint8Array recibe un ArrayBuffer y devuelve un array de enteros sin signo de 8 bits
      //El mtodo map() crea un nuevo array con los resultados de la llamada a la funcin indicada aplicados a cada uno de sus elementos
      //El mtodo toString(16) convierte un nmero a una cadena, representando el nmero en base 16
      //El mtodo padStart() rellena la cadena actual con una cadena dada (repetida, si es necesario) de modo que la cadena resultante alcance una longitud dada (2)
      return [...new Uint8Array(buffer)].map((b) => b.toString(16).padStart(2,"0")).join("");
    };

    const handleLogin = async (e) => {
        e.preventDefault();

        //Con la biblioteca crypto-browserify
        // const hash = createHash("sha3-256").update(password).digest("hex");
        // console.log("Hash generado: ", hash);

        //Con la Web Crypto API
        const encoder = new TextEncoder(); //Para convertir la cadena a un arreglo de bytes
        const datos = encoder.encode(password); //.encode() convierte la cadena a un arreglo de bytes
        const hashBuffer = await window.crypto.subtle.digest("SHA-256", datos); //Genera el hash de la contrasea
        //.digest() recibe el nombre de la funcin hash a utilizar y los datos en forma de: ArrayBuffer, TypedArray, DataView o Buffer
        //Adems, regresa una promesa que resuelve en un ArrayBuffer que contiene el hash
        //Imprimimos el hash original
        console.log("Hash original: ", hashBuffer);

        //Convertimos el ArrayBuffer a hexadecimal
        const hashHex = aBufferToHex(hashBuffer);
        console.log("Hash hexadecimal: ", hashHex);

        //Enviamos el hash y usuario al servidor
        const respuesta = await fetch("https://practicaloginback.onrender.com/login",{
            method: "POST",
            headers:{
                "content-type": "application/json",
            },
            body: JSON.stringify({
                username,
                password: hashHex
            }),
        });

        const respuesta2 = await respuesta.json();
        console.log("Datos recibidos: ", respuesta2);

        //-----------------------Redirigir a Home Page----------------------------
        if(respuesta2.message == "Login exitoso"){
          console.log("Redirigiendo a Home");
          navigate("/home", {state: {username: username}});
        }
    };

    return (
      <div>
        <BarraSuperiorInicio />
        <div className="mainContainer">
          <div>
            <h1 className="titulo">Iniciar Sesin</h1>
          </div>
          <div className="card-datos">
            <div className="">
              <form onSubmit={handleLogin}>
                <div className="cuadroDatos">
                  <label>Usuario*:</label>
                  <input
                    type="text"
                    placeholder="Nombre de usuario"
                    value={username}
                    onChange={(e) => setUsername(e.target.value)}
                    className="card-input"
                    required
                  />
                </div>
                <div className="cuadroDatos">
                  <label>Contrasea*:</label>
                  <input
                    type="password"
                    // placeholder="Contrasea"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    className="card-input"
                    required
                  />
                </div>
                <div className="centrar">
                  <button type="submit" className="boton">
                    Iniciar sesin
                  </button>
                </div>
              </form>
              <div className="parrafoCentrar">
                <p>
                  An no tienes cuenta?{" "}
                  <Link to="/registro" className="link-primary">
                    Crea una aqu
                  </Link>
                </p>
                <p>
                  <Link to="/recuperar-password" className="link-primary">
                    Olvid mi contrasea
                  </Link>
                </p>
              </div>
            </div>
          </div>
        </div>
        <FooterPG />
      </div>
    );
};

export default FormsLogin;