import React, {useState} from "react";

import {Link, useNavigate} from "react-router-dom";

import { BarraSuperiorCerrar } from "./BarraSuperiorCerrar";
import { FooterPG } from "./Footer";
import "../CSS/contenedor.css";

import { jsPDF } from "jspdf";

const Fact2Page = () => {

    const generatePDF = () => {
        const doc = new jsPDF();
        doc.setFontSize(16);
        doc.text("Criptografa de clave pblica: la base de Internet", 105, 10, null, null, "center");
        doc.setFontSize(12);
        doc.text(
          `Los lmites de la criptografa simtrica:
Antes de la invencin de la criptografa de clave pblica, la mayora de los sistemas dependan de la criptografa simtrica, donde una sola clave se utilizaba tanto para cifrar como para descifrar un mensaje. Este enfoque presentaba un gran problema logstico: para garantizar la seguridad, era necesario transmitir la clave entre el remitente y el receptor a travs de un canal seguro. En la prctica, esto no siempre era posible y, si la clave era interceptada, todo el sistema de comunicacin quedaba comprometido.

La revolucin conceptual:
En 1976, Whitfield Diffie y Martin Hellman introdujeron el concepto de la criptografa de clave pblica con su protocolo de intercambio de claves, conocido como Diffie-Hellman. Poco despus, en 1978, Ron Rivest, Adi Shamir y Leonard Adleman desarrollaron el algoritmo RSA, que implement este concepto de manera prctica. La idea central era simple pero poderosa: cada usuario tendra dos claves matemticamente relacionadas, una pblica y otra privada.

    Clave pblica: Se comparte abiertamente y se utiliza para cifrar mensajes.
    Clave privada: Se mantiene en secreto y se utiliza para descifrar los mensajes cifrados con la clave pblica correspondiente.

Aplicaciones modernas y protocolos:
La criptografa de clave pblica es fundamental para muchas tecnologas que usamos diariamente:

    HTTPS (HyperText Transfer Protocol Secure): Protege la comunicacin entre navegadores y servidores web, asegurando que los datos sensibles, como contraseas y tarjetas de crdito, estn cifrados durante las transacciones en lnea.
    Cadenas de bloques y criptomonedas: Las firmas digitales, basadas en la criptografa de clave pblica, garantizan que solo el propietario legtimo de una billetera puede autorizar transacciones.
    Infraestructura de clave pblica (PKI): Utilizada en sistemas de autenticacin, como certificados digitales, para validar la identidad de sitios web, organizaciones y personas.

Impacto social y econmico:
La criptografa de clave pblica no solo ha transformado la seguridad en lnea, sino que tambin ha permitido la globalizacin del comercio digital, el desarrollo de tecnologas como el Internet de las cosas (IoT) y la proteccin de datos personales frente a amenazas cada vez ms sofisticadas.`,
          10,
          30,
          { maxWidth: 180 }
        );
        doc.text(" RegiSecure. Todos los derechos reservados", 105, 280, null, null, "center");
        doc.save("PublicKey.pdf");
      };

    return(
        <div>
            <BarraSuperiorCerrar />
            <div className="mainContainer">
                <div>
                    <h1 className="titulo">Criptografa de clave pblica: la base de Internet</h1>
                </div>
                <div className="card-datos justificado">
                    <p>
                        Los lmites de la criptografa simtrica:
                        Antes de la invencin de la criptografa de clave pblica, la mayora de los sistemas dependan de la criptografa simtrica, donde una sola clave se utilizaba tanto para cifrar como para descifrar un mensaje. Este enfoque presentaba un gran problema logstico: para garantizar la seguridad, era necesario transmitir la clave entre el remitente y el receptor a travs de un canal seguro. En la prctica, esto no siempre era posible y, si la clave era interceptada, todo el sistema de comunicacin quedaba comprometido.
                    </p>
                    <p>
                        La revolucin conceptual:
                        En 1976, Whitfield Diffie y Martin Hellman introdujeron el concepto de la criptografa de clave pblica con su protocolo de intercambio de claves, conocido como Diffie-Hellman. Poco despus, en 1978, Ron Rivest, Adi Shamir y Leonard Adleman desarrollaron el algoritmo RSA, que implement este concepto de manera prctica. La idea central era simple pero poderosa: cada usuario tendra dos claves matemticamente relacionadas, una pblica y otra privada.
                    </p>
                    <p>
                        Clave pblica: Se comparte abiertamente y se utiliza para cifrar mensajes.
                    </p>
                    <p>
                        Clave privada: Se mantiene en secreto y se utiliza para descifrar los mensajes cifrados con la clave pblica correspondiente.
                    </p>
                    <p>
                        Aplicaciones modernas y protocolos:
                    </p>
                    <p>
                        La criptografa de clave pblica es fundamental para muchas tecnologas que usamos diariamente:

                        HTTPS (HyperText Transfer Protocol Secure): Protege la comunicacin entre navegadores y servidores web, asegurando que los datos sensibles, como contraseas y tarjetas de crdito, estn cifrados durante las transacciones en lnea.
                        Cadenas de bloques y criptomonedas: Las firmas digitales, basadas en la criptografa de clave pblica, garantizan que solo el propietario legtimo de una billetera puede autorizar transacciones.
                        Infraestructura de clave pblica (PKI): Utilizada en sistemas de autenticacin, como certificados digitales, para validar la identidad de sitios web, organizaciones y personas.
                    </p>
                    <p>
                        Impacto social y econmico:
                        La criptografa de clave pblica no solo ha transformado la seguridad en lnea, sino que tambin ha permitido la globalizacin del comercio digital, el desarrollo de tecnologas como el Internet de las cosas (IoT) y la proteccin de datos personales frente a amenazas cada vez ms sofisticadas.
                    </p>
                    <div className="centrar">
                        <button onClick={generatePDF} className="boton">
                            Generar PDF
                        </button>
                    </div>
                </div>
            </div>
            <FooterPG />
        </div>
    );
};

export default Fact2Page