import React, { useState } from "react";
//Biblioteca para la validacion de contraseas, aparte de usar regex, ser zxcvbn
import zxcvbn from "zxcvbn";

import { useNavigate } from 'react-router-dom';

import { BarraSuperiorInicio } from "./BarraSuperiorInicio";
import { FooterPG } from "./Footer";
import "../CSS/contenedor.css";

//Reglas para la complejidad de la contrasea
//Minimo de caracteres: 12
//Al menos dos letras maysculas
//Al menos dos letras minsculas
//Al menos dos nmeros
//Al menos dos caracteres especiales

const Register = () => {
    const [username, setUsername] = useState("");
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [passwordConfirm, setPasswordConfirm] = useState("");
    const [mensajeError, setMensajeError] = useState('');

    const navigate = useNavigate();

    const aBufferToHex = (buffer) => {
        return [...new Uint8Array(buffer)].map((b) => b.toString(16).padStart(2,"0")).join("");
    };

    //Para la validacin de la contrasea con las reglas establecidas
    const regex = /^(?=.*[a-z]{2,})(?=.*[A-Z]{2,})(?=.*[0-9]{2,})(?=.*[\W_]{2,}).{12,}$/;


    const handleRegister = async (e) => {
        e.preventDefault();

        //Validamos la contrasea con las reglas establecidas
        if(!regex.test(password)){
            //Mostramos un mensaje abajo del input de la contrasea
            setMensajeError("La contrasea no cumple con las reglas establecidas");
            return;
        }

        //Checamos que las dos contraseas sean iguales
        if(password !== passwordConfirm){
            //Mostramos un mensaje abajo del input de la confirmacin de la contrasea
            setMensajeError("Las contraseas no coinciden");
            return;
        }

        //Checamos la fortaleza de la contrasea con zxcvbn
        const resultado = zxcvbn(password);
        console.log("Fortaleza de la contrasea: ", resultado.score);

        if(resultado.score < 3){
            //Mostramos un mensaje abajo del input de la contrasea
            setMensajeError("La contrasea es muy dbil");
            return;
        }

        setMensajeError("");

        const encoder = new TextEncoder(); //Para convertir la cadena a un arreglo de bytes
        const datos = encoder.encode(password); //.encode() convierte la cadena a un arreglo de bytes
        const hashBuffer = await window.crypto.subtle.digest("SHA-256", datos); //Genera el hash de la contrasea

        //Imprimimos el hash original
        console.log("Hash original: ", hashBuffer);

        //Convertimos el ArrayBuffer a hexadecimal
        const hashHex = aBufferToHex(hashBuffer);
        console.log("Hash hexadecimal: ", hashHex);

        //Enviamos al back el correo para que se mande el correo de verificacion antes de hacer el registro
        const verificacionRes = await fetch("https://practicaloginback.onrender.com/verificacion", {
            method: "POST",
            headers: {
                "content-type": "application/json",
            },
            body: JSON.stringify({
                email
            }),
        });

        const verificacionQuery = await verificacionRes.json();

        //Checamos si no hubo error en el envio del correo con el cdigo de verificacin
        if(verificacionQuery.error){
            console.log("Error al enviar el correo de verificacin: ", verificacionQuery.error);
            return;
        }

        console.log("Correo enviado correctamente, redirigiendo a la pantalla de verificacin");
        //Redirigimos a la pantalla de verificacion, pero mandamos el correo, la contrasea en hash, username y el cdigo de verificacin
        navigate('/verificacion', {state: {email, hashHex, username, code: verificacionQuery.token, hora: verificacionQuery.hora, expira: verificacionQuery.expira}});
    };

    return(
        <div>
            <BarraSuperiorInicio />
            <div className="mainContainer">
                <div>
                    <h1 className="titulo">Registro</h1>
                </div>
                <div>
                    <div className="parrafoCentrar">
                        <p>Recuerda que la contrasea debe cumplir con las siguientes reglas:</p>
                    </div>
                    <div className="NoBottom">
                    <ul>
                        <li>Al menos 12 caracteres</li>
                        <li>Al menos dos letras maysculas</li>
                        <li>Al menos dos letras minsculas</li>
                        <li>Al menos dos nmeros</li>
                        <li>Al menos dos caracteres especiales</li>
                    </ul>
                    </div>
                </div>
                <div className="card-datos">
                    <form onSubmit={handleRegister}>
                        <div className="cuadroDatos">
                            <label>Nombre de Usuario*:</label>
                            <input
                                type="text"
                                className="card-input"
                                placeholder="JuanBrcs"
                                value={username}
                                onChange={(e) => setUsername(e.target.value)}
                                required
                            />
                        </div>
                        <div className="cuadroDatos">
                            <label>Correo electrnico*:</label>
                            <input
                                type="email"
                                className="card-input"
                                placeholder="example@gmail.com"
                                value={email}
                                onChange={(e) => setEmail(e.target.value)}
                                required
                            />
                        </div>
                        <div className="cuadroDatos">
                            <label>Contrasea*:</label>
                            <input
                                type="password"
                                className="card-input"
                                value={password}
                                onChange={(e) => setPassword(e.target.value)}
                                required
                            />
                        </div>
                        <div className="cuadroDatos">
                            <label>Confirmar contrasea*:</label>
                            <input
                                type="password"
                                className="card-input"
                                value={passwordConfirm}
                                onChange={(e) => setPasswordConfirm(e.target.value)}
                                required
                            />
                        </div>
                        
                        <div className='parrafoCentrar'>
                            {mensajeError && <p style={{color: 'red'}}>{mensajeError}</p>}
                        </div>
                        <div className="centrar">
                            <button type="submit" className="boton">Registrarse</button>
                        </div>
                    </form>
                </div>
            </div>
            <FooterPG />
        </div>
    );
};

export default Register;