import React, { useState } from 'react'; 
import { useNavigate } from 'react-router-dom'; 
import zxcvbn from 'zxcvbn'; 

import { BarraSuperiorInicio } from "./BarraSuperiorInicio";
import { FooterPG } from "./Footer";
import "../CSS/contenedor.css";

//Reglas para la complejidad de la contrasea
//Minimo de caracteres: 12
//Al menos dos letras maysculas
//Al menos dos letras minsculas
//Al menos dos nmeros
//Al menos dos caracteres especiales

const RestablecerContrasea = () => {
    const [usuario, setUsuario] = useState('');
    const [paso, setPaso] = useState('solicitarRestablecimiento');
    const [tokenRestablecimiento, setTokenRestablecimiento] = useState('');
    const [nuevaContrasea, setNuevaContrasea] = useState('');
    const [confirmarContrasea, setConfirmarContrasea] = useState('');
    const [mensajeError, setMensajeError] = useState('');

    const navegacion = useNavigate();

    const aBufferToHex = (buffer) => {
        return [...new Uint8Array(buffer)].map((b) => b.toString(16).padStart(2, "0")).join("");
    };

    //Para la validacin de la contrasea con las reglas establecidas
    const regex = /^(?=.*[a-z]{2,})(?=.*[A-Z]{2,})(?=.*[0-9]{2,})(?=.*[\W_]{2,}).{12,}$/;

    const manejarSolicitudRestablecimiento = async (e) => {
        e.preventDefault();
        setMensajeError('');

        try {
            const respuesta = await fetch('https://practicaloginback.onrender.com/solicitar-restablecimiento', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ 
                    username: usuario
                }),
            });

            const datos = await respuesta.json();

            if (respuesta.ok) {
                setPaso('introducirTokenRestablecimiento');
            } else {
                setMensajeError(datos.error || 'Error en la solicitud de restablecimiento');
            }
        } catch (error) {
            setMensajeError('Error de conexin');
            console.error('Error:', error);
        }
    };

    const manejarVerificacionTokenRestablecimiento = async (e) => {
        e.preventDefault();
        setMensajeError('');

        try {
            const respuesta = await fetch('https://practicaloginback.onrender.com/verificar-token-restablecimiento', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ 
                    username: usuario,
                    tokenRestablecimiento 
                }),
            });

            const datos = await respuesta.json();

            if (respuesta.ok) {
                setPaso('restablecerContrasea');
            } else {
                setMensajeError(datos.error || 'Token invlido');
            }
        } catch (error) {
            setMensajeError('Error de conexin');
            console.error('Error:', error);
        }
    };

    const manejarRestablecimientoContrasea = async (e) => {
        e.preventDefault();
        setMensajeError('');

        const fortalezaContrasea = zxcvbn(nuevaContrasea);
        if (fortalezaContrasea.score < 3) {
            setMensajeError('La contrasea es muy dbil');
            return;
        }

        if (nuevaContrasea !== confirmarContrasea) {
            setMensajeError('Las contraseas no coinciden');
            return;
        }

        //Validamos la contrasea con las reglas establecidas
        if(!regex.test(nuevaContrasea)){
            //Mostramos un mensaje abajo del input de la contrasea
            setMensajeError("La contrasea no cumple con las reglas establecidas");
            return;
        }

        try {
            const codificador = new TextEncoder();
            const datos = codificador.encode(nuevaContrasea);
            const bufferHash = await window.crypto.subtle.digest("SHA-256", datos);
            const hashHex = aBufferToHex(bufferHash);

            const respuesta = await fetch('https://practicaloginback.onrender.com/restablecer-contrasena', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ 
                    username: usuario,
                    tokenRestablecimiento,
                    nuevaContrasea: hashHex 
                }),
            });

            const respuestaDatos = await respuesta.json();

            if (respuesta.ok) {
                navegacion('/');
            } else {
                setMensajeError(respuestaDatos.error || 'Error al restablecer la contrasea');
            }
        } catch (error) {
            setMensajeError('Error de conexin');
            console.error('Error:', error);
        }
    };

    const renderizarPaso = () => {
        switch(paso) {
            case 'solicitarRestablecimiento':
                return (
                    <div>
                        <BarraSuperiorInicio />
                        <div className='mainContainer'>
                            <div>
                                <h1 className="titulo">Restablecer Contrasea</h1>
                            </div>
                            <div className='card-datos'>
                                <form onSubmit={manejarSolicitudRestablecimiento}>
                                    <div className="cuadroDatos">
                                        <label>Nombre de Usuario*:</label>
                                        <input
                                            type="text"
                                            value={usuario}
                                            onChange={(e) => setUsuario(e.target.value)}
                                            className="card-input"
                                            required
                                        />
                                    </div>
                                    <div className='centrar'>
                                        <button type="submit" className='boton'>Solicitar Restablecimiento</button>
                                    </div>
                                    <div className='parrafoCentrar'>
                                        {mensajeError && <p style={{color: 'red'}}>{mensajeError}</p>}
                                    </div>
                                </form>
                            </div>
                        </div>
                        <FooterPG />
                    </div>
                );
            
            case 'introducirTokenRestablecimiento':
                return (
                    <div>
                        <BarraSuperiorInicio />
                        <div className='mainContainer'>
                            <div>
                                <h2 className="titulo">Verificar Cdigo de Restablecimiento</h2>
                            </div>
                            <div className='card-datos'>
                                <form onSubmit={manejarVerificacionTokenRestablecimiento}>
                                    <div className='parrafoCentrar'>
                                        <p>Hemos enviado un cdigo de restablecimiento a tu correo electrnico.</p>
                                    </div>
                                    <div className="cuadroDatos">
                                        <label>Cdigo de Restablecimiento*:</label>
                                        <input
                                            type="text"
                                            value={tokenRestablecimiento}
                                            onChange={(e) => setTokenRestablecimiento(e.target.value)}
                                            className="card-input"
                                            required
                                        />
                                    </div>
                                    <div className='centrar'>
                                        <button type="submit" className='boton'>Verificar Cdigo</button>
                                    </div>
                                    <div className='parrafoCentrar'>
                                        {mensajeError && <p style={{color: 'red'}}>{mensajeError}</p>}
                                    </div>
                                </form>
                            </div>
                        </div>
                        <FooterPG />
                    </div>
                );
            
            case 'restablecerContrasea':
                return (
                    <div>
                        <BarraSuperiorInicio />
                        <div className='mainContainer'>
                            <div>
                                <h2 className='titulo'>Restablecer Contrasea</h2>
                            </div>
                            <div>
                                <div className="parrafoCentrar">
                                    <p>Recuerda que la contrasea debe cumplir con las siguientes reglas:</p>
                                </div>
                                <div className="NoBottom">
                                <ul>
                                    <li>Al menos 12 caracteres</li>
                                    <li>Al menos dos letras maysculas</li>
                                    <li>Al menos dos letras minsculas</li>
                                    <li>Al menos dos nmeros</li>
                                    <li>Al menos dos caracteres especiales</li>
                                </ul>
                                </div>
                            </div>
                            <div className='card-datos'>
                                <form onSubmit={manejarRestablecimientoContrasea}>
                                    <div className="cuadroDatos">
                                        <label>Nueva Contrasea:</label>
                                        <input
                                            type="password"
                                            value={nuevaContrasea}
                                            onChange={(e) => setNuevaContrasea(e.target.value)}
                                            className="card-input"
                                            required
                                        />
                                    </div>
                                    <div className="cuadroDatos">
                                        <label>Confirmar Nueva Contrasea:</label>
                                        <input
                                            type="password"
                                            value={confirmarContrasea}
                                            onChange={(e) => setConfirmarContrasea(e.target.value)}
                                            className="card-input"
                                            required
                                        />
                                    </div>
                                    <div className='centrar'>
                                        <button type="submit" className='boton'>Restablecer Contrasea</button>
                                    </div>
                                    <div className='parrafoCentrar'>
                                        {mensajeError && <p style={{color: 'red'}}>{mensajeError}</p>}
                                    </div>
                                </form>
                            </div>
                        </div>
                        <FooterPG />
                    </div>
                );
            
            default:
                return null;
        }
    };

    return (
        <div>
            {renderizarPaso()}
        </div>
    );
};

export default RestablecerContrasea;