import React, {useState} from "react";

import { jsPDF } from "jspdf";

import { BarraSuperiorCerrar } from "./BarraSuperiorCerrar";
import { FooterPG } from "./Footer";
import "../CSS/contenedor.css";

const Fact1Page = () => {

    const generatePDF = () => {
        const doc = new jsPDF();
        doc.setFontSize(16);
        //Titulo centrado
        doc.text("La mquina Enigma y la Segunda Guerra Mundial", 105, 10, null, null, "center");

        // doc.text("La mquina Enigma y la Segunda Guerra Mundial", 10, 20);
        doc.setFontSize(12);
        //Texto justificado
        doc.text(
          `Orgenes y diseo de Enigma:
La mquina Enigma fue diseada inicialmente para propsitos comerciales por Arthur Scherbius en la dcada de 1920. Su potencial para proteger comunicaciones pronto llam la atencin de los militares, particularmente de Alemania, que comenz a adaptarla y perfeccionarla para sus necesidades blicas. La versin militar introdujo un sistema de rotores adicionales y configuraciones diarias que incrementaban exponencialmente el nmero de combinaciones posibles, alcanzando cifras astronmicas que hacan virtualmente imposible descifrar un mensaje sin conocer la configuracin exacta.

Cmo funcionaba:
El operador configuraba los rotores de Enigma segn una clave diaria proporcionada en un manual cifrado. Luego, al escribir el mensaje en el teclado, cada letra se transformaba en otra diferente segn la posicin de los rotores y un reflector interno. Esta complejidad haca que cada vez que se pulsaba una tecla, el sistema generara una salida distinta. Sin embargo, la mquina tambin tena una vulnerabilidad inherente: ninguna letra poda cifrarse como s misma, un defecto que los criptgrafos aliados aprovecharon para descifrarla.

El papel de Polonia y los avances en Bletchley Park:
Antes de la guerra, los criptografos polacos Marian Rejewski, Jerzy Rozycki y Henryk Zygalski lograron avances cruciales al reconstruir una versin de Enigma basndose en inteligencia obtenida de espas y en su habilidad matemtica. Compartieron este conocimiento con los britnicos y los franceses en 1939, poco antes de la invasin alemana a Polonia. En Bletchley Park, Alan Turing y su equipo desarrollaron una mquina llamada "Bombe" para automatizar el proceso de descifrado.

Impacto en la guerra y la tecnologa:
Descifrar Enigma permiti a los Aliados interceptar y anticipar los movimientos alemanes, incluidos los ataques de los submarinos en el Atlntico. Esta ventaja estratgica salv miles de vidas y acort significativamente la guerra. Adems, los esfuerzos en Bletchley Park marcaron el inicio de la computacin moderna, ya que las tecnologas desarrolladas para descifrar Enigma influyeron directamente en el diseo de las primeras computadoras electrnicas.`,
          10,
          30,
          { maxWidth: 180 }
        );
        //Agregamos el nombre de la empresa
        doc.text(" RegiSecure. Todos los derechos reservados", 105, 280, null, null, "center");

        doc.save("Enigma.pdf");
      };

    return(
        <div>
            <BarraSuperiorCerrar />
            <div className="mainContainer">
                <div>
                    <h1 className="titulo">La mquina Enigma y la Segunda Guerra Mundial</h1>
                </div>
                <div className="card-datos justificado">
                    <p>
                        Orgenes y diseo de Enigma:
                        La mquina Enigma fue diseada inicialmente para propsitos comerciales por Arthur Scherbius en la dcada de 1920. Su potencial para proteger comunicaciones pronto llam la atencin de los militares, particularmente de Alemania, que comenz a adaptarla y perfeccionarla para sus necesidades blicas. La versin militar introdujo un sistema de rotores adicionales y configuraciones diarias que incrementaban exponencialmente el nmero de combinaciones posibles, alcanzando cifras astronmicas que hacan virtualmente imposible descifrar un mensaje sin conocer la configuracin exacta.
                    </p>
                    <p>
                        Cmo funcionaba:
                        El operador configuraba los rotores de Enigma segn una clave diaria proporcionada en un manual cifrado. Luego, al escribir el mensaje en el teclado, cada letra se transformaba en otra diferente segn la posicin de los rotores y un reflector interno. Esta complejidad haca que cada vez que se pulsaba una tecla, el sistema generara una salida distinta. Sin embargo, la mquina tambin tena una vulnerabilidad inherente: ninguna letra poda cifrarse como s misma, un defecto que los criptgrafos aliados aprovecharon para descifrarla.
                    </p>
                    <p>
                        El papel de Polonia y los avances en Bletchley Park:
                        Antes de la guerra, los criptgrafos polacos Marian Rejewski, Jerzy Rycki y Henryk Zygalski lograron avances cruciales al reconstruir una versin de Enigma basndose en inteligencia obtenida de espas y en su habilidad matemtica. Compartieron este conocimiento con los britnicos y los franceses en 1939, poco antes de la invasin alemana a Polonia. En Bletchley Park, Alan Turing y su equipo desarrollaron una mquina llamada "Bombe" para automatizar el proceso de descifrado.
                    </p>
                    <p>
                        Impacto en la guerra y la tecnologa:
                        Descifrar Enigma permiti a los Aliados interceptar y anticipar los movimientos alemanes, incluidos los ataques de los submarinos en el Atlntico. Esta ventaja estratgica salv miles de vidas y acort significativamente la guerra. Adems, los esfuerzos en Bletchley Park marcaron el inicio de la computacin moderna, ya que las tecnologas desarrolladas para descifrar Enigma influyeron directamente en el diseo de las primeras computadoras electrnicas.
                    </p>
                    <div className="centrar">
                        <button onClick={generatePDF} className="boton">
                            Generar PDF
                        </button>
                    </div>
                </div>
            </div>
            <FooterPG />
        </div>
    );
};

export default Fact1Page
